* {
  font-family: Verdana, Arial, Helvetica, sans-serif;
}
.header {
  background-color: #5190ea;
  color: #fff;
  width: 100%;
  box-shadow: 0px 8px 8px -8px #000;
  padding: 0.5%;
}

input {
  padding: 0.5%;
  margin-top: 2%;
  width: 50%;
  font-family: Arial, sans-serif;
}
button {
  background-color: #5190ea;
  padding: 0.5%;
  color: #fff;
  border: 1px solid #5190ea;
}

.container {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}
p {
  color: #f7881a;
  font-size: 14px;
}

.child {
  border: 1px solid #ddd;
  margin: 1%;
  padding: 1%;
  width: 20%;
  font-size: 11px;
  border-radius: 5%;
}

.child:hover {
  box-shadow: 0px 8px 8px -8px #000;
}

.img-responsive {
  border-radius: 50%;
}

@media only screen and (min-width: 320px) and (max-width: 480px) {
  .child {
    width: 90%;
    font-size: 14px;
  }
}

@media only screen and (min-width: 481px) and (max-width: 769px) {
  .child {
    width: 45%;
  }
}
